<template>
  <!--  eslint-disable -->
  <div>
    <!-- Modal para Agregar Centro de Costos -->
    <validation-observer 
      ref="costcenter"
      #default="{invalid}">
      <b-modal
        id="modal-1"
        ref="costCenterModal"
        hide-footer
        title="Agregar Centro de Costos"
        scrollable
        @hidden="hideCostCenterModal">
        <ErrorValidation v-if="validationErrors" :errors="validationErrors" />
        <div class="d-block">        
            <form @submit.prevent="addCostCenter">
              <div class="form-row">
                <!-- Código -->
                <div class="form-group col-md-12">
                  <label for="code">Código</label>
                  <validation-provider
                    #default="{ errors }"
                    name="código"
                    rules="required|alpha-num"
                  >
                    <b-form-input
                      id="code"
                      v-model="cost_center.code"
                      type="text"
                      class="form-control"
                      placeholder="Código del Centro de Costos"
                      :state="errors.length > 0 ? false:null"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </div>

              </div>

              <div class="form-row">

                <!-- Descripción -->
                <div class="form-group col-md-12">
                  <label for="description">Descripción</label>
                  <validation-provider
                    #default="{ errors }"
                    name="descripción"
                    rules="required|alpha_accents"
                  >
                    <b-form-input
                      id="description"
                      v-model="cost_center.description"
                      type="text"
                      class="form-control"
                      placeholder="Descripción del Centro de Costos"
                      :state="errors.length > 0 ? false:null"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </div>

              </div>

              <hr>

              <div class="text-right">
                <button
                  type="button"
                  class="btn btn-danger btn-sm mx-1"
                  @click="hideCostCenterModal"
                >
                  Cancelar
                </button>
                <button
                  type="submit"
                  class="btn btn-primary btn-sm"
                  :disabled="invalid"
                >
                  <span class="fa fa-check" /> Guardar
                </button>
              </div>
            </form>        
        </div>
      </b-modal>
    </validation-observer>
  </div>
</template>

<script>

/*eslint-disable*/

// Notificaciones
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

// Servicios
import * as costCenterService from '@/services/cost_center'

export default {

  name: 'AddCostCenter',

  components: {
    ToastificationContent,
  },

  data() {
    return {
      validationErrors: '',
      cost_center: {
        code: '',
        description: '',
      },
    }
  },

  methods: {

    async addCostCenter() {
      try {
        const response = await costCenterService.addCostCenter(this.cost_center)

        if (response.status === 200) {
          this.hideCostCenterModal()
          this.$emit('reload')
          this.showToast('Agregado', 'CheckIcon', 'Centro de Costo Agregado', 'success')
        }
      } catch (error) {
          if (error.response.status === 422) {
            this.validationErrors = error.response.data.errors;
          } else {
            this.showToast('Advertencia!', 'AlertCircleIcon', 'Ocurrió un error al agregar el Centro de Costos', 'warning')
          }
        }
      },

    hideCostCenterModal() {
      this.$refs.costcenter.reset()
      this.$refs.costCenterModal.hide()
      this.cleanForm()
    },

    cleanForm() {
      this.validationErrors = ''      
      this.cost_center = {
        code: '',
        description: '',
      }
    },

    showToast(title, icon, text, variant) {
      this.$toast({
        component: ToastificationContent,
        props: {
          title,
          icon,
          text,
          variant,
        },
      })
    },

  },
}

</script>

<style>

</style>
