<template>
  <!--  eslint-disable -->
  <div>
    <validation-observer 
      ref="costcenter"
      #default="{invalid}">
      <b-modal
        id="editCostCenter"
        ref="editModal"
        hide-footer
        title="Editar Centro de Costo"
        scrollable
        >
        <ErrorValidation v-if="validationErrors" :errors="validationErrors" />
        <div class="d-block">
          <form @submit.prevent="updateCostCenter">

            <div class="form-row">

              <!-- Código -->
              <div class="form-group col-md-12">
                <label for="edit_code">Código</label>
                <validation-provider
                    #default="{ errors }"
                    name="código"
                    rules="required|alpha-num"
                  >
                  <b-form-input
                    id="edit_code"
                    v-model="editCostCenter.code"
                    type="text"
                    class="form-control"
                    :state="errors.length > 0 ? false:null"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </div>

            </div>

            <div class="form-row">

              <!-- Descripción -->
              <div class="form-group col-md-12">
                <label for="edit_description">Descripción</label>
                <validation-provider
                    #default="{ errors }"
                    name="descripción"
                    rules="required|alpha_accents"
                  >
                  <b-form-input
                    id="edit_description"
                    v-model="editCostCenter.description"
                    type="text"
                    class="form-control"
                    :state="errors.length > 0 ? false:null"
                  />
                   <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </div>

            </div>

            <hr>

            <div class="text-right">
              <button
                type="button"
                class="btn btn-danger btn-sm mx-1"
                @click="hideModal()"
              >
                Cancelar
              </button>
              <button
                type="submit"
                class="btn btn-primary btn-sm"
                :disabled="invalid"
              >
                <span class="fa fa-check" /> Actualizar
              </button>
            </div>
          </form>
        </div>
      </b-modal>
    </validation-observer>
  </div>
</template>

<script>

/*eslint-disable*/

// Notificaciones
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

// Servicios
import * as costCenterService from '@/services/cost_center'

export default {

  props: {
    editCostCenter: {
      type: Object,
      default: () => {},
    },
  },

  data() {
    return {
      validationErrors: '',
    }
  },

  methods: {

    async updateCostCenter() {
      try {
        const response = await costCenterService.updateCostCenter(this.editCostCenter.id, this.editCostCenter)

        if (response.status === 200) {          
          this.hideModal()
          this.$emit('reload')
          this.showToast('Actualizado', 'CheckIcon', 'Centro de Costo Actualizado', 'success')
        }
      } catch (error) {
         if (error.response.status === 422) {
            this.validationErrors = error.response.data.errors;
          } else {
            this.showToast('Advertencia!', 'AlertCircleIcon', 'Ocurrió un error al actualizar el Centro de Costos', 'warning')
          }
      }
    },

    hideModal() {
      this.$refs.costcenter.reset()
      this.validationErrors = ''
      this.$refs.editModal.hide()
      this.$emit('reload')       
    },

    showToast(title, icon, text, variant) {
      this.$toast({
        component: ToastificationContent,
        props: {
          title,
          icon,
          text,
          variant,
        },
      })
    },

  },

}

</script>
